@import '../styles/_variables';

/* src/sections/Hero.module.scss */

$navbar-height: 4rem; // Debe coincidir con Navbar.module.scss
$bg-gray-900: #111827;
$bg-teal-900-alpha: rgba(19, 78, 74, 0.3);
$text-white: #ffffff;
$text-gray-300: #d1d5db;
$teal-400: #2dd4bf;
$azure-400: #38bdf8;
$emerald-500: #10b981;

@keyframes animatedGradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-15px); }
}

@keyframes breathe {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 0.7; transform: scale(1.1); }
}

.heroSection {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  min-height: 600px;
  text-align: center;
  overflow: hidden;
  padding-top: $navbar-height; // pt-16 para compensar navbar fija
}

.backgroundContainer {
  position: absolute;
  inset: 0;
  z-index: 0;
}

.animatedGradient {
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, $bg-gray-900, $bg-teal-900-alpha, $bg-gray-900);
  background-size: 400% 400%;
  animation: animatedGradient 15s ease infinite;
}

/* Fondo de imagen */
.backgroundImage {
  position: absolute;
  inset: 0;
  background-image: url('../images/background.webp');
  background-size: cover;
  background-position: center;
  opacity: 0.4; /* Aumentado para una mejor visibilidad */
  z-index: 1; /* Asegurar que está por encima del gradiente pero por debajo del contenido */
}

.glowOrbs {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 2;
}

.orb {
  position: absolute;
  border-radius: 50%;
  filter: blur(30px);
  opacity: 0.3;
  z-index: 2;
  animation: breathe 10s infinite ease-in-out;
  
  &:nth-child(1) {
    top: 20%;
    left: 10%;
    width: 180px;
    height: 180px;
    background: radial-gradient(circle, rgba(45, 212, 191, 0.7), transparent 70%);
    animation-delay: 0s;
  }
  
  &:nth-child(2) {
    top: 60%;
    right: 10%;
    width: 220px;
    height: 220px;
    background: radial-gradient(circle, rgba(56, 189, 248, 0.6), transparent 70%);
    animation-delay: 3s;
  }
  
  &:nth-child(3) {
    bottom: 15%;
    left: 20%;
    width: 150px;
    height: 150px;
    background: radial-gradient(circle, rgba(16, 185, 129, 0.5), transparent 70%);
    animation-delay: 6s;
  }
}

.contentContainer {
  position: relative;
  z-index: 10;
  padding-left: 1rem; // px-4
  padding-right: 1rem;
}

.title {
  position: relative;
  font-size: 1.75rem; // Reducido para móviles
  font-weight: 900; // font-black
  color: $text-white;
  margin-bottom: 1.5rem; // mb-6
  line-height: 1.1; // leading-tight
  letter-spacing: -0.03em;
  text-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  animation: float 6s ease-in-out infinite;
  
  .highlight {
    display: inline-block;
    position: relative;
    background: linear-gradient(45deg, $teal-400, $azure-400);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 8px;
      background: linear-gradient(45deg, rgba(45, 212, 191, 0.3), rgba(56, 189, 248, 0.3));
      border-radius: 4px;
      z-index: -1;
    }
  }

  @media (min-width: 640px) { // sm:
    font-size: 3rem; // sm:text-5xl
  }
  @media (min-width: 768px) { // md:
    font-size: 3.75rem; // md:text-6xl
  }
  @media (min-width: 1024px) { // lg:
    font-size: 4.5rem; // lg:text-7xl
  }
}

.subtitle {
  font-size: $font-md;
  color: $text-white; /* Cambiado para mejor contraste con la imagen de fondo */
  max-width: 56rem; // max-w-3xl (aproximado)
  margin-left: auto;
  margin-right: auto;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
  line-height: 1.6;
  position: relative;
  z-index: 1;
  text-align: center;
  text-transform: uppercase;
  font-weight: $font-medium;
  letter-spacing: 0.05em;

  @media (min-width: $breakpoint-sm) { // sm:
    font-size: $font-lg;
  }
  @media (min-width: $breakpoint-md) { // md:
    font-size: $font-xl;
  }
}

/* Estilos para botón CTA (si se descomenta en JSX) */
.ctaButton {
  margin-top: 2.5rem; // mt-10
  padding: 0.9rem 2.25rem; // px-9 py-3.5
  background: linear-gradient(135deg, rgba(45, 212, 191, 0.9), rgba(56, 189, 248, 0.9));
  color: $text-white;
  font-weight: 600; // font-semibold
  border-radius: 0.5rem; // rounded-lg
  box-shadow: 0 10px 25px -5px rgba(45, 212, 191, 0.3);
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  font-size: 1.1rem;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    transition: left 0.7s ease;
  }

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 15px 30px -5px rgba(45, 212, 191, 0.4);
    
    &::before {
      left: 100%;
    }
  }
  
  &:active {
    transform: translateY(1px);
  }
}

/* Ilustración de la red de fibra óptica */
.fiberIllustration {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 180px;
  width: 100%;
  max-width: 800px;
  margin: 2rem auto 3rem; // Espacio entre el título, la ilustración y el subtítulo
  
  @media (max-width: $breakpoint-sm) { 
    height: 120px; // Altura más pequeña en móviles
    margin: 1.5rem auto 2rem;
    width: 330px;
  }
}

// Red de fibra óptica completa
.fiberNetwork {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}

// Línea de fibra (conexión)
.fiberConnection {
  position: absolute;
  top: 50%; // Centrar verticalmente en el contenedor
  transform: translateY(-50%); // Asegurar centrado preciso
  height: 3px; // Aumentado ligeramente
  width: 100%;
  background: linear-gradient(to right, 
    rgba(239, 68, 68, 0.3), 
    rgba(16, 185, 129, 0.3), 
    rgba(59, 130, 246, 0.3), 
    rgba(234, 179, 8, 0.3)
  );
  z-index: 1;
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.3);
}

// Efecto adicional de destello para la línea de fibra cuando pasa el pulso
@keyframes fiberGlow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(239, 68, 68, 0.3);
  }
  25% {
    box-shadow: 0 0 10px rgba(16, 185, 129, 0.5);
  }
  50% {
    box-shadow: 0 0 15px rgba(59, 130, 246, 0.7);
  }
  75% {
    box-shadow: 0 0 10px rgba(234, 179, 8, 0.5);
  }
}

.fiberConnection::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: fiberGlow 4s infinite alternate;
  z-index: 0;
}

// Elementos de la red (nodos)
.networkNode {
  position: absolute;
  z-index: 10; // Asegurar que esté sobre la línea de fibra
  transform: translateX(-50%); // Centrar el nodo en su posición horizontal
  top: 50%; // Centrar verticalmente con la línea de fibra
}

// Punto central con círculo
.nodePoint {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: $bg-gray-900;
  border: 2px solid;
  position: relative;
  z-index: 5; // Aumentado para asegurar que esté por encima de la línea
  box-shadow: 0 0 8px rgba(16, 185, 129, 0.6);
  transform: translateY(-50%); // Centrar con la línea de fibra
  transition: all 0.3s ease;
  
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: currentColor; // Tomará el color del nodo
    box-shadow: 0 0 5px currentColor;
    transition: all 0.3s ease;
  }
}

// Efecto de brillo cuando el nodo está activo
.nodePoint.glowing {
  transform: translateY(-50%) scale(1.15);
  box-shadow: 
    0 0 10px rgba(255, 255, 255, 0.8),
    0 0 15px currentColor;
  
  &::after {
    width: 8px;
    height: 8px;
    box-shadow: 0 0 8px currentColor, 0 0 15px rgba(255, 255, 255, 0.8);
  }
}

// Etiqueta de cada nodo
.nodeLabel {
  position: absolute;
  top: 16px; // Ajustado para estar más cerca del punto
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.7rem;
  white-space: nowrap;
  color: $text-white;
  font-weight: 500;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

// Icono superior
.nodeIcon {
  position: absolute;
  bottom: 40px; // Aumentado para que aparezca más arriba
  left: 50%;
  transform: translateX(-50%);
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: $bg-gray-900;
  border-radius: 50%;
  border: 1px solid rgba(45, 212, 191, 0.3);
  color: $teal-400;
  box-shadow: 0 0 10px rgba(45, 212, 191, 0.4);
  z-index: 6; // Añadido para que esté por encima de todo
}

// Central
.central {
  .nodePoint {
    width: 20px;
    height: 20px;
    border-color: #ef4444; // Rojo
  }
  .nodeIcon {
    color: #ef4444; // Rojo
    box-shadow: 0 0 10px rgba(239, 68, 68, 0.5);
  }
}

// POP (Punto de Presencia)
.pop {
  .nodePoint {
    border-color: #10b981; // Verde
  }
  .nodeIcon {
    color: #10b981; // Verde
    box-shadow: 0 0 10px rgba(16, 185, 129, 0.5);
  }
}

// DP (Distribuidor Principal)
.dp {
  .nodePoint {
    border-color: #3b82f6; // Azul
  }
  .nodeIcon {
    color: #3b82f6; // Azul
    box-shadow: 0 0 10px rgba(59, 130, 246, 0.5);
  }
}

// Cliente
.client {
  .nodePoint {
    width: 14px;
    height: 14px;
    border-color: #eab308; // Amarillo más brillante
  }
  .nodeIcon {
    color: #eab308; // Amarillo más brillante
    box-shadow: 0 0 10px rgba(234, 179, 8, 0.5);
  }
}

// Animación para el pulso de luz con pausas en cada nodo
@keyframes pulse {
  0% {
    left: -15px;
    opacity: 0.8;
  }
  5% {
    opacity: 1;
  }
  
  // Pausa en el primer nodo (0%)
  22% {
    left: 0%;
  }
  24% {
    left: 0%;
    transform: scale(1.2);
  }
  
  // Pausa en el segundo nodo (33%)
  38% {
    left: 33.3%;
  }
  40% {
    left: 33.3%;
    transform: scale(1.2);
  }
  
  // Pausa en el tercer nodo (66%)
  62% {
    left: 66.6%;
  }
  64% {
    left: 66.6%;
    transform: scale(1.2);
  }
  
  // Pausa en el cuarto nodo (100%)
  88% {
    left: 100%;
    opacity: 1;
  }
  90% {
    left: 100%;
    opacity: 1;
    transform: scale(1.2);
  }
  
  100% {
    left: 110%;
    opacity: 0.2;
  }
}

// Animación del brillo de los nodos cuando pasa el pulso
@keyframes nodeGlow {
  0%, 100% {
    box-shadow: 0 0 8px rgba(16, 185, 129, 0.6);
    transform: translateY(-50%) scale(1);
  }
  50% {
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.8);
    transform: translateY(-50%) scale(1.15);
  }
}

// Aplicar animación a cada nodo cuando el pulso pasa
.central .nodePoint {
  animation: nodeGlow 0.5s ease-in-out;
  animation-delay: 1.3s; // Sincronizado con el paso del pulso
  animation-iteration-count: infinite;
  animation-play-state: paused;
}

.pop .nodePoint {
  animation: nodeGlow 0.5s ease-in-out;
  animation-delay: 2.3s; // Sincronizado con el paso del pulso
  animation-iteration-count: infinite;
  animation-play-state: paused;
}

.dp .nodePoint {
  animation: nodeGlow 0.5s ease-in-out;
  animation-delay: 3.7s; // Sincronizado con el paso del pulso
  animation-iteration-count: infinite;
  animation-play-state: paused;
}

.client .nodePoint {
  animation: nodeGlow 0.5s ease-in-out;
  animation-delay: 5.2s; // Sincronizado con el paso del pulso
  animation-iteration-count: infinite;
  animation-play-state: paused;
}

// Pulso de luz que recorre la fibra
.lightPulse {
  position: absolute;
  top: 50%; 
  transform: translateY(-50%);
  left: 0;
  width: 15px;
  height: 6px;
  border-radius: 4px;
  background: #ffffff;
  filter: blur(1px);
  box-shadow: 
    0 0 8px rgba(255, 255, 255, 0.9),
    0 0 15px rgba(255, 255, 255, 0.7),
    0 0 25px rgba(255, 255, 255, 0.5);
  animation: pulse 6s infinite ease-in-out;
  z-index: 10;
}

.lightPulse:hover ~ .networkNode .nodePoint {
  animation-play-state: running;
}

// Pequeñas partículas adicionales
.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background-color: rgba(45, 212, 191, 0.8);
  border-radius: 50%;
  top: 50%; // Alinear con la línea
  transform: translateY(-50%); // Centrar precisamente
  z-index: 3;
  animation: particleTravel 8s linear infinite;
}

// Datos transmitidos (efecto de partículas)
.dataParticles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
  
  .particle {
    position: absolute;
    width: 3px;
    height: 3px;
    background: #ffffff; // Cambiado a blanco puro
    border-radius: 50%;
    top: 50%; // Alineado con la línea
    transform: translateY(-50%);
    opacity: 0;
    filter: blur(1px);
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.8);
    
    @for $i from 1 through 12 {
      &:nth-child(#{$i}) {
        animation: particleTravel 6s infinite;
        animation-delay: $i * 0.3s;
      }
    }
  }
}

// Animación del pulso de luz viajando
@keyframes pulseTravel {
  0% {
    left: 0%;
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  95% {
    opacity: 1;
  }
  100% {
    left: 100%;
    opacity: 0;
  }
}

// Animación de las partículas de datos
@keyframes particleTravel {
  0% {
    left: -5%;
    opacity: 0;
  }
  10% {
    opacity: 0.7;
  }
  90% {
    opacity: 0.7;
  }
  100% {
    left: 105%;
    opacity: 0;
  }
}

.particle:nth-child(1) {
  animation-delay: 0.5s;
}

.particle:nth-child(2) {
  animation-delay: 1.5s;
}

.particle:nth-child(3) {
  animation-delay: 2.5s;
} 