@import '../styles/_variables';

/* src/sections/Gallery.module.scss */

$text-white: #ffffff;
$breakpoint-sm: 640px;
$breakpoint-lg: 1024px;
$teal-400: #2dd4bf;
$azure-400: #38bdf8;
$emerald-400: #34d399;

@keyframes floatIn {
  0% { transform: translateY(20px); opacity: 0; }
  100% { transform: translateY(0); opacity: 1; }
}

.gallerySection {
  padding-top: 5rem; // py-20
  padding-bottom: 5rem;
  background-color: $bg-gray-800-alpha;
  position: relative;
  
  &::after {
    content: '';
    position: absolute;
    width: 200px;
    height: 200px;
    background: radial-gradient(circle, rgba(56, 189, 248, 0.1), transparent 70%);
    right: 10%;
    top: 15%;
    border-radius: 50%;
    filter: blur(40px);
  }
}

.container {
  max-width: 1280px; // max-w-7xl
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem; // px-4
  padding-right: 1rem;
  @media (min-width: 640px) { // sm:
    padding-left: 1.5rem; // sm:px-6
    padding-right: 1.5rem;
  }
  @media (min-width: 1024px) { // lg:
    padding-left: 2rem; // lg:px-8
    padding-right: 2rem;
  }
}

.titleContainer {
  position: relative;
  text-align: center;
  padding-bottom: 3rem;
  margin-bottom: 3rem;
  overflow: hidden;
  
  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 1px;
    background: linear-gradient(to right, $teal-400, $azure-400);
  }
}

.titleBackground {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 8rem;
  font-weight: 900;
  opacity: 0.04;
  color: $teal-400;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  white-space: nowrap;
}

.title {
  font-size: 1.875rem; // text-3xl
  font-weight: 800; // font-bold
  color: $text-white;
  position: relative;
  display: inline-block;
  z-index: 1;
  margin-bottom: 1rem;
  
  .highlight {
    position: relative;
    z-index: 1;
    color: transparent;
    background: linear-gradient(90deg, $teal-400, $azure-400);
    -webkit-background-clip: text;
    background-clip: text;
  }
  
  @media (min-width: $breakpoint-sm) { // sm:
    font-size: 2.5rem; // sm:text-4xl
  }
}

.titleDecoration {
  position: relative;
  width: 30px;
  height: 30px;
  display: inline-block;
  margin: 0 1rem;
  
  &::before,
  &::after {
    content: '';
    position: absolute;
    background: $teal-400;
  }
  
  &::before {
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    transform: translateY(-50%);
  }
  
  &::after {
    left: 50%;
    top: 0;
    width: 2px;
    height: 100%;
    transform: translateX(-50%);
  }
}

.subtitle {
  color: rgba(255, 255, 255, 0.7);
  font-size: $font-md;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
  position: relative;
  z-index: 1;
  animation: floatIn 0.8s ease-out forwards;
  text-align: center;
  text-transform: uppercase;
  font-weight: $font-medium;
  letter-spacing: 0.05em;
  
  @media (min-width: $breakpoint-sm) { // sm:
    font-size: $font-lg;
  }
  @media (min-width: $breakpoint-md) { // md:
    font-size: $font-xl;
  }
}

.grid {
  display: grid;
  grid-template-columns: 1fr; // grid-cols-1
  gap: 1.5rem; // gap-6

  @media (min-width: $breakpoint-sm) { // sm:
    grid-template-columns: repeat(2, 1fr); // sm:grid-cols-2
  }

  @media (min-width: $breakpoint-lg) { // lg:
    grid-template-columns: repeat(3, 1fr); // lg:grid-cols-3
    gap: 2rem; // md:gap-8 (usando lg aquí)
  }
}

