@import '../styles/variables';

/* Estilos para simular un OTDR */
.otdrContainer {
  display: none; /* Oculto por defecto, se mostrará en pantallas grandes */
  justify-content: center;
  align-items: center;
  padding: 2rem 0;

  @media (min-width: $breakpoint-md) { /* Mostrar solo en medium y superior */
    display: flex;
  }
}

.otdrBody {
  background-color: #3a4a5a; /* Color base del cuerpo del OTDR */
  border-radius: 15px;
  padding: 25px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3), inset 0 2px 4px rgba(255, 255, 255, 0.1);
  border: 1px solid #2c3a47;
  width: 100%;
  max-width: 950px; /* Ancho máximo del OTDR */
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.screen {
  background-color: #1a202c; /* Fondo oscuro de la pantalla */
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  aspect-ratio: 16 / 9; /* Proporción de la pantalla */
  border: 3px solid #4a5568; /* Marco de la pantalla */
  box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.5);
}

.image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Cubrir el espacio de la pantalla */
}

.screenOverlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(255,255,255,0.03), rgba(255,255,255,0.01));
    pointer-events: none; /* Para no interferir con la imagen */
}

.controls {
  background-color: #2d3748; /* Panel de controles */
  padding: 15px;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #4a5568;
}

.brand {
    font-weight: bold;
    color: $teal-400;
    font-size: 1.1rem;
    text-transform: uppercase;
}

.modelInfo {
    font-size: 0.8rem;
    color: #a0aec0; /* Cambiado de $gray-400 a un valor de color directo */
}


.buttonGroup {
    display: flex;
    gap: 10px;
}

.button {
  background-color: #4a5568;
  color: $text-white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.1s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);

  &:hover {
    background-color: #5a6578;
  }

  &:active {
    transform: scale(0.95);
  }
}

/* Detalles estéticos */
.vents {
    position: absolute;
    bottom: 10px;
    left: 25px;
    width: 60px;
    height: 10px;
    background: repeating-linear-gradient(
        to right,
        #2d3748,
        #2d3748 5px,
        #3a4a5a 5px,
        #3a4a5a 10px
    );
    border-radius: 3px;
}

.handle {
    position: absolute;
    top: 50%;
    right: -20px; /* Sobresale un poco */
    transform: translateY(-50%);
    width: 40px;
    height: 100px;
    background-color: #2d3748;
    border-radius: 0 10px 10px 0;
    border: 1px solid #4a5568;
    border-left: none;
    box-shadow: 2px 0 5px rgba(0,0,0,0.2);
} 